.App {
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

body {
  margin: 0;
  background-color: #F2F3AE;
}

h1, h2, h3 {
  font-family: 'Castellar', 'Times New Roman', serif;
  text-transform: uppercase;
}

h4 {
  text-transform: uppercase;
}

header {
  text-align: center;
  background: url(3859499.jpg), #69140E;
  background-size:cover;
  color:whitesmoke;
  border:5px double white;
  
}

.gridContainer {
  max-height:70vw;
  overflow-y:scroll;
  padding-bottom:15px;
}

.grid{
  display:grid;
  gap: 10px;
  grid-template-columns: repeat(3, 100px);
  grid-auto-rows:minmax(100px);
  width:min-content;
  margin-left:auto;
  margin-right:auto;
  
}

.portrait {
  border:3px ridge #A44200;
  box-shadow: 0px 5px 10px;
  overflow: hidden;
  position:relative;
  padding:0;
  height:100px; 
}

.tint{
  position:absolute;
  width:100px;
  height:100px;
  opacity:0.5;
  transition: opacity 0.5s;
  background-color: rgb(0, 0, 0);
}

.portrait-content img {
  height:100px;
  width:100px;
  opacity:1;
}

.tint:hover{
  opacity:0;
}

main {
  text-align:center;
  padding:10px;
}

#info {
  height:45%;
  padding: 10px;
  color:black;
}

.toggleContainer {
  position:relative;
  margin-bottom: 20px;
}

#recapToggle {
  border: 5px double white;
  position:absolute;
  top:0px;
  right:0px;
  width:40%;
  background: url(3859499.jpg), #69140E;
  color:white;
  padding: 0 10px 0 10px;
}

#recap {
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  max-width:1fr;
  overflow:scroll;
  word-wrap: break-word;
  background-color:#F2F3AE;
  border: 5px double white;
  z-index: 4;
  padding:10px;
}

.recap-enter {
  opacity:0;
  transform: scale(0.9);
}

.recap-enter-active {
  opacity:1;
  transform: translateX(0);
  transition: opacity 400ms, transform 300ms;
}

.recap-exit {
  opacity: 1;
}

.recap-exit-active {
  opacity: 0;
  transition: opacity 400ms;
}

#closeRecap {
  position:absolute;
  right:10px;
  top: 0;
  color:white;
  width:30px;
  height:30px;
  border:5px double white;
  
}

aside * {
  margin: 0;
}

.infoPortrait{
  border:10px ridge rgb(71, 46, 0);
  display:block;
  margin: 10px auto 0 auto;
}

footer {
  padding: 10px;
  text-align: center;
  border-top: 1px solid slategray;
  margin-top: 10px;
}

button{
  font-family: inherit;
  border:0;
  height:auto;
  width:50%;
  background-color: #69140E;
  color:white;
  display:block;
  margin: 10px auto 10px auto;
  border: 5px double white;
}


footer a {
  text-decoration: underline;
}